	onLoad() {
		uni.getSystemInfo({
			success: (res)=> {
				let height = res.windowHeight - uni.upx2px(40);
				this.swiperheight = height
			}
		});
		this.TowerSwiper('swiperList');
		// 初始化towerSwiper 传已有的数组名即可
		
	},
	onBackPress() {
		if (this.type !== '') {
			this.type = ''
			return true
		}
	},
	// 搜索框跳转
	onNavigationBarSearchInputClicked() {
		uni.navigateTo({
